import React from "react";

import { AiFillInstagram, AiFillFacebook, AiOutlineWhatsApp } from "react-icons/ai";
import { Link } from "react-router-dom";

import './footerComponent.css';

function FooterComponent() {
    return (
        <div className="footer">
            <div className="line-detail"></div>
            <div className="footer-component">
                <div className="info-collumn">
                    <div className="image-frame"></div>
                    <div className="description">
                                            Ajudamos voc a encontrar o equilbrio de sua sade fsica e mental com muito amor, paixo e entrega no que fazemos.
                                            Tenha a certeza que estaremos com nosso foco em nosso propsito, para que voc esteja cada vez melhor!</div>
                </div>
                <div className="contact-collumn">
                    <div className="title">Contatos:</div>
                    <div><a className="bold-subtitle">Endereo:</a> endereo teste, 123</div>
                    <div><a className="bold-subtitle">Telefone:</a> +55 15 996774312</div>
                    <div><a className="bold-subtitle">Email:</a> +55 15 996774312</div>
                </div>
                <div className="service-hour-collumn">
                    <div className="title">Horrios de atendimento:</div>
                    <div><a className="bold-subtitle">Dias disponveis:</a> seg  sex</div>
                    <div><a className="bold-subtitle">Horrios:</a> 8:00 as 14:00</div>
                    <div><a className="bold-subtitle">Preferencia para contato via Whatsapp!</a></div>
                </div>
                <div className="social-media-frame">
                    <div className="title">Siga-nos nas redes sociais!</div>
                    <div className="medias">
                        <Link to="/"> <AiFillInstagram className="icon-media"/> </Link>
                        <Link to="/"> <AiFillFacebook className="icon-media"/> </Link>
                        <Link to="/"> <AiOutlineWhatsApp className="icon-media"/> </Link>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default FooterComponent;