import React from 'react';
import ReactDOM from 'react-dom';

import {BrowserRouter as Router, Route, Routes} from 'react-router-dom';
import Toolbar from './components/Toolbar/Toolbar';
import ArticlesPage from './pages/Artigos/ArticlesPage';
import HomePage from './pages/Home/HomePage';
import LojaPage from './pages/Loja/LojaPage';
import ProductPage from './pages/Produto/ProductPage';
import SobreNosPage from './pages/SobreNos/SobreNos';
import ArticlePage from './pages/ArtigoPage/ArtigoPage';

export default function App() {
    return (
        <Router>
            <Toolbar/>
            <Routes>
                <Route path="/Renoveterapias/" exact element={<HomePage/>} />
                <Route path="/Renoveterapias/Loja" exact element={<LojaPage/>} />
                <Route path="/Renoveterapias/Artigos" exact element={<ArticlesPage/>} />
                <Route path="/Renoveterapias/Produto/:id" exact element={<ProductPage/>} />
                <Route path="/Renoveterapias/Artigo/:id" exact element={<ArticlePage/>} />
                <Route path="/Renoveterapias/SobreNos" exact element={<SobreNosPage/>} />
            </Routes>
        </Router>
    )
}