import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';

import { AiOutlineMessage, AiOutlineHeart, AiOutlineArrowRight } from 'react-icons/ai';

import ArticleComponent from '../../components/articleComponent';
import './ArticlesPage.css';
import FooterComponent from '../../components/Footer/footerComponent';


function ArticlesPage(props) {
    const [loading, setLoading] = useState(true);
    const [articleList, setArticleList] = useState([]);
    const [article, setArticle] = useState([]);
    const [image, setImage] = useState();

    //let { id } = useParams();

    const backend_url = 'https://app-teste256.herokuapp.com' //http://localhost:5000  https://app-teste256.herokuapp.com
    
    const mainArticle = '6226496552687454e51e379d'

    const fetchData = async () => {
        const response = await fetch(`${backend_url}/article/${mainArticle}`);
        const newData = await response.json();
        setArticle(newData)
        setImage(newData.url_image)

        const responseList = await fetch(`${backend_url}/articles/0/10`);
        const newDataList = await responseList.json();
        setArticleList(newDataList)
        
        setLoading(false)
    };

    useEffect(() => {
        fetchData();
        window.scrollTo({
             top: 0,
             behavior: 'smooth'
         });
    }, [])
    
    return (
        <div>
            <div className='line-articles'>
            <div className='container-article'>
                <div className='articles'>
                    <div className='first-article'>
                        <div className='text-collumn-article'>
                            <div className='title-article'>{article.title}</div>
                            <div className='date-article'>{article.date}</div>
                            <div className='description-article'>{article.description}</div>
                            <div className='collum-buttons-article'>
                                <button><AiOutlineHeart/> Like</button>
                                <button><AiOutlineMessage/> Postar um comentrio</button>
                                <div className='read-more-button'><button>LER MAIS <AiOutlineArrowRight/></button></div>
                            </div>
                        </div>
                        <div className='image-collumn-article'>
                            <img className='image-article' src={`${window.location.href.split('/')[0]}//${window.location.href.split('/')[2]}/${window.location.href.split('/')[3]}${image}`}/>
                        </div>
                    </div>
                    <p className='title-default-section'>ARTIGOS RECENTES</p>
                    {Object.keys(articleList).map(function(object, index) {
                        if(loading || articleList[object]._id === mainArticle) {return} else {
                            return <div><ArticleComponent title={articleList[object].title} date={articleList[object].date} description={articleList[object].description} image={`${window.location.href.split('/')[0]}//${window.location.href.split('/')[2]}/${window.location.href.split('/')[3]}${articleList[object].url_image}`} key={index.toString()} id={articleList[object]._id} /></div>
                        }
                    })}
                </div>
                <div className='profile'>
                    <div className='div-image-profile'>
                        <img src={`${window.location.href.split('/')[0]}//${window.location.href.split('/')[2]}/${window.location.href.split('/')[3]}/images/Artigos/profile-photo.png`}/>
                    </div>
                    <p className='title-profile'>Ns somos a Renoveterapia</p>
                    <div className='description-profile'>Nosso Propsito  Transformar voc em sua melhor verso, de forma natural.
                                        Ajudamos voc a encontrar o equilbrio de sua sade fsica e mental com muito amor, paixo e entrega no que fazemos.
                                        Tenha a certeza que estaremos com nosso foco em nosso propsito, para que voc esteja cada vez melhor!</div>
                    </div>
                </div>
                <FooterComponent />
            </div>
        </div>
    );
}

export default ArticlesPage;